import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Home from './components/Home.vue'
import forUs from './components/forUs.vue'
import gateList from './components/gateList.vue'
import handRail from './components/handRail.vue'
import detail from './components/detail.vue'

Vue.use(VueRouter)

const routes = [
  {path:'/home',component:Home},
  // redirect:'/home',
  {path:'/forUs',component:forUs},
  {path:'/gateList',component:gateList},
  {path:'/handRail',component:handRail},
  {path:'/detail/:id',component:detail},
  //1、页面要匹配一个动态路由，使用：id，id为动态路径的参数
  //2、这个设置完了以后，通过参数值会被设置到this.$route.params
  {path:'*',redirect:'/'}//这个路由是当用户输入错误时调到的页面，类似404页面
]


const router = new VueRouter({
  mode: 'history',
  linkActiveClass:'active',
  // base: process.env.BASE_URL,
  routes,

})

router.push('/home')

export default router
